<template>
  <div id="edit-employe">
  <h2> Edit Employee </h2>
  <div class="row">
  <form @submit.prevent="updateEmployee" class="col s12">
  <div class="row">
  <div class="input-field col s6">  
   <input disabled type="text" v-model="empid" required>
  


 </div>
<div class="input-field col s6">  
   <input type="text" v-model="name" required>
   


 </div>


 <div class="input-field col s6">  
   <input type="text" v-model="dept" required>
   


 </div>


<div class="input-field col s6">  
   <input type="text" v-model="position" required>
  


 </div>

  </div>


      <button type="submit" class="btn green"> Submit </button>
      <router-link to="/" class="btn red"> Cancel </router-link>

  </form>
  </div>
  </div>
</template>

<script>
import db from './firebaseInit'

export default {
    name:'edit-employe',
    data(){
        return{
          
          empid:null,
          name:null,
          dept:null,
          position:null

        }
        
        },

        beforeRouteEnter (to, from, next) {
          db.collection('employees').where('empid','==',to.params.empid).get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                vm.empid = doc.data().empid 
                vm.name = doc.data().name
                vm.dept = doc.data().dept
                vm.position = doc.data().position
                




              
                
                
                
                
                })



              }
              )


            }


          )





        },
        watch:{
          '$route':'fetchData'
        },

        methods:{
          fetchData(){
            db.collection('employees').where('empid','==',
            this.params.employee_id).get().then(
              querySnapshot => {
                querySnapshot.forEach(doc => {
                  this.employee_id=doc.data().empid
                  this.name=doc.data().name
                  this.dept=doc.data().dept
                  this.position=doc.data().position




                })


              })
            


          },

          updateEmployee(){
            db.collection('employees').where('empid','==',
            this.params.empid).get().then(
              querySnapshot => {
                querySnapshot.forEach(doc => {
                  doc.ref.update({
                    
                    name:this.name,
                    dept:this.dept,
                    position:this.position






                  })
                  .then(() => {
                    this.$router.push({name :'view-employee',
                    params: {empid:this.empid}
                    
                    })
                  }) 
                })
          })       
          }     
        }
}
</script>


