<template>
  <div id="view-employe">
  <ul class="collection with-header">
  <li class="collection-header"> 
  <h4> {{name}}</h4>
</li> 

<li class="collection-item">Employee ID: {{empid}}</li>
<li class="collection-item">Departement: {{dept}}</li>
<li class="collection-item">Poisiton: {{position}}</li>
</ul>
<router-link to="/" class="btn teal"> Back </router-link>
<button @click="deleteEmployee" class="btn red">  Delete </button>
<div class="fixed-action-btn">
<router-link v-bind:to="{name:'edit-employe', params:{empid:empid}}" class="btn-floating btn-large teal">
  <i class="fa fa-pencil"> </i>
 </router-link>
</div>

  </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name:'view-employe',
    data(){
        return{
          
          empid:null,
          name:null,
          dept:null,
          position:null
          
          

        }
        
        },
        

        beforeRouteEnter (to, from, next) {
          db.collection('employees').where('empid','==',to.params.employee_id).get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                vm.empid = doc.data().empid 
                vm.name = doc.data().name
                vm.dept = doc.data().dept
                vm.position = doc.data().position
                




              
                
                
                
                
                })



              }
              )


            }


          )





        },
        watch:{
          '$route':'fetchData'
        },

        methods:{
          fetchData(){
            db.collection('employees').where('empid','==',
            this.params.employee_id).get().then(
              querySnapshot => {
                querySnapshot.forEach(doc => {
                  this.employee_id=doc.data().empid
                  this.name=doc.data().name
                  this.dept=doc.data().dept
                  this.position=doc.data().position




                })


              })
            


          },
          deleteEmployee () {
            if (confirm ('are you sure you want to delete this employee ?')) {
              db.collection('employees').where('empid','==',
            this.$route.params.employee_id).get().then(
              querySnapshot => {
                querySnapshot.forEach(doc => {
                  doc.ref.delete()
                  this.$router.push('/')

                  
            })

            
          })


        }
          }
        }


}
</script>


