<template>
<nav>
  <div class="nav-wrapper teal">
        <div class="container">
        <router-link to="/" class="brand-logo"> Rh Manager </router-link>
        <ul class="right">
        <li v-if="isLoggedIn"> <span class="email white-text" > {{currentUser}} </span> </li>
        <li v-if="isLoggedIn"> <router-link  to="/"> Dashboard </router-link>  </li>
          <li v-if="!isLoggedIn"> <router-link  to="/login"> Login</router-link>  </li>
            <li v-if="!isLoggedIn"> <router-link to="/register"> register </router-link>  </li>
        <li v-if="isLoggedIn"> <button v-if="isLoggedIn" v-on:click="logout" class="btn teal"> Logout  </button>   </li>



        



        </ul>




        </div>
  </div>

</nav>
</template>


<script>
import firebase from 'firebase'
export default {
  name:'navbar',

  data(){
    return {
      isLoggedIn:false,
      currentUser:false
    }





     },
     created(){
       if (firebase.auth().currentUser){
         this.isLoggedIn=true;
         this.currentUser = firebase.auth().currentUser.email;


       }

    },




     methods: {
       logout:function (){
         firebase.auth().signOut().then( () => {
           this.$router.push('/login');



         }
          );
       }
     }
  }



</script>

<style scoped>
.email {padding-right:10px;}

</style>



