<template>
  <div>
    <div class="container">
      <div class="row">
        <div class="col s12 m8 offset -m2"></div>
        <div class="login card-panel teal white-text center"></div>
        <h3> Login </h3>
        <form>
        <div class="input-field">
        
        <input type="text" id="email" v-model='email'>
        <label for="email"> E-mail </label>
        </div>

            <div class="input-field">
        
        <input type="password" id="lock" v-model='password'>
        <label for="password"> password </label>
        </div>


        <button  v-on:click="login" class="btn btn-large green  white-text"> login </button>




         </form>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from 'firebase'
export default{
  name:'login' ,
 data : function (){
     return {
       email:'',
       password:''
     };
 },
 methods:{
     login:function (e) {
       firebase.auth().signInWithEmailAndPassword(this.email, this.password).then
       (user => {
         alert('you are logged in as ${user.email}');
         this.$router.push('/')


       },
       err => {
         alert(err.message);
       }

       )
       e.preventDefault();





     }

 }
}
</script>
<style scoped>
h3{
  font-family:'Roboto' ;
  font-weight:bold;
  color:cadetblue;
  
}

</style>



