import Vue from 'vue'
import Router from 'vue-router'
import dashboard from '../components/dashboard.vue'
import ViewEmployee from '../components/ViewEmployee.vue'
import EditEmployee from '../components/EditEmploye.vue'
import NewEmployee from '../components/newEmploye.vue'
import login from '../components/Login.vue'
import  register from '../components/register.vue'
import 'boxicons'
import firebase from 'firebase' ;




Vue.use(Router)

let router = new Router({
  routes: [
    {
      path: '/',
      name: 'Dashboard',
      component: dashboard,
      meta: {
        requiresAuth:true
      }
    },


    {
      path :'/login',
      name :'login' ,
      component: login,
      meta: {
        requiresGuest:true
      }


    },

    {
      path :'/register',
      name :'register' ,
      component: register,
      meta: {
        requiresGuest:true
      }


    },



    {
      path: '/new',
      name: 'new-employee',
      component: NewEmployee,
      meta: {
        requiresAuth:true
      }
    },

    {
      path: '/edit/:employe_id',
      name: 'edit-employe',
      component: EditEmployee,
      meta: {
        requiresAuth:true
      }
    },

    {
      path: '/:employee_id',
      name: 'view-employee',
      component: ViewEmployee ,
      meta: {
        requiresAuth:true
      }
    },

  ]
});

router.beforeEach((to, from, next ) =>{
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if(!firebase.auth().currentUser){
      next ({
        path : '/login',
        query :{
          redirect:to.fullPath
        }  }    
        );

    } else {
      next();


    }

  }else if (to.matched.some(record => record.meta.requiresGuest)){
    if (firebase.auth().currentUser){
      next ({
        path :'' ,
        query :{
          redirect:to.fullPath


        }




      });

    }else {
      next();
    }

  }else {
    next();
  }



    });

    export default router;



  




    



  














